.main-foot{
    padding: 2rem;
    background-color:#FAF5E4;
    text-align: center;
}

.footer{
    display: flex;
}

.address-head{
    font-size: 1.5rem;
    font-weight: 600;
    display:inline;
    border: 0.1rem solid black;
    padding: 0.3rem;
    border-radius: 0.2rem;
}

.add{
    color: #646672;

    padding: 1rem;
}

.add-head{
    font-size: large;
    font-weight: 800;
}

.icons{
    list-style: none;
}

.icons li{
    display: inline;
    padding: 0.3rem;
}

.address{
    flex-grow: 4;
}

.options{
    flex-grow: 2;
    color: #646672;

}

.option_name{
    list-style: none;
}

.option_name li{
    display: inline;
    padding: 2.5rem;
    margin: 2rem;
}

.foot{
    text-align: center;
    font-size: 0.7rem;
    color: #646672;
}






/* Responsive layout - makes a one column layout instead of a two-column layout */
@media (max-width: 900px) {
    .footer {
      flex-direction: column;
    }

    .option_name li{
        display: block;
        padding: 0rem;
        margin: 0rem;
    }

    .main-foot{
        padding: 1rem;
        background-color:#FAF5E4;
    }
  }