.pricing{
    padding: 5rem 1rem;
}

.cards{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.pricing-head{
    font-size: 3rem;
    text-align: center;
    font-weight: 700;
    font-family: 'Ubuntu', sans-serif;
}

.card{
    width: 25rem;
    border: 0.3rem solid black;
}

.card-head{
    padding: 1rem 0rem;
    font-size: 2rem;
    font-weight: 700;
    text-align: center;
    font-family: 'Ubuntu', sans-serif;
}

.card-price{
    font-size: 2rem;
    font-weight: 700;
    text-align: center;
}


.price-ul{
    list-style: none;
}

.active{
    font-size: 1.2rem;
    font-weight: 500;
    list-style-type: disc;
}

.inactive{
    font-size: 1.2rem;
    font-weight: 500;
    color: gray;
}

.c1{
    background-color: rgb(200, 255, 255);
}

.c2{
    background-color: rgb(227, 173, 85);
}

.c3{
    background-color: whitesmoke;
}

.button{
    text-align: center;
    padding: 2rem 0.5rem;
}

.price-btn{
    background: linear-gradient(86.31deg, #FF5C6B 0%, #EB9898 99.29%);
    width: 15rem;
    padding: 0.8rem 2.4rem;
    border-radius: 0.5rem;
    font-size: 1.2rem;
    color: white;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    line-height: 136%;
    text-align: center;
    letter-spacing: -0.02em;
}

.price-btn:hover{
    background: #3cb0fd;
  background-image: -webkit-linear-gradient(top, #3cb0fd, #3498db);
  background-image: -moz-linear-gradient(top, #3cb0fd, #3498db);
  background-image: -ms-linear-gradient(top, #3cb0fd, #3498db);
  background-image: -o-linear-gradient(top, #3cb0fd, #3498db);
  background-image: linear-gradient(to bottom, #3cb0fd, #3498db);
  text-decoration: none;
}

@media screen and (max-width: 900px) {

    .pricing{
        padding: 2rem 0rem;
    }
    .cards{
        grid-template-columns: auto;
    }

    .card{
        width: 20rem;
    }

    .price-btn{
        width: 13rem;
    }
  }

  @media screen and (min-width: 700px) {
    .cards{
        grid-template-columns: auto;
    }

    .card{
        width: 40rem;
    }

    .price-btn{
        width: 13rem;
    }
  }

  @media screen and (min-width: 1200px) {

    .cards{
        grid-template-columns: auto auto auto;
    }

    .card{
        width: 20rem;
    }

    .price-btn{
        width: 13rem;
    }
  }