.cover{
    /* height: 25rem; */
    text-align: center;
    padding: 8rem 0rem 6rem 0rem;
    background: repeat-x;
    background-image: url('https://img.freepik.com/free-photo/christmas-background-with-snowflakes-glowing-stars-designs_1048-13430.jpg?w=1380&t=st=1686480704~exp=1686481304~hmac=3c45517e95d48a1559fd88fa14d10675e4567bc1af809fe7845b2dd2fdc21a05');
    border-top-left-radius: 2rem;
    border-top-right-radius: 2rem;
    font-family: 'Merriweather', serif;
}

.cover-heading1{
    font-size: 3rem;
    color: gray;
    font-weight: bolder;
}

.cover-heading2{
    font-size: 3.5rem;
    color: green;
    font-weight: bolder;
}

.cover-text{
    font-size: 0.8rem;
    margin: 0rem 20rem;
}

.cover-button{
    padding-top: 1rem;
}

.cover-btn{
    background: linear-gradient(86.31deg, #FF5C6B 0%, #EB9898 99.29%);
    padding: 0.8rem 2.4rem;
    border-radius: 0.5rem;
    font-size: 1.2rem;
    color: white;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    line-height: 136%;
    text-align: center;
    letter-spacing: -0.02em;
}

.cover-btn:hover{
    background: #3cb0fd;
  background-image: -webkit-linear-gradient(top, #3cb0fd, #3498db);
  background-image: -moz-linear-gradient(top, #3cb0fd, #3498db);
  background-image: -ms-linear-gradient(top, #3cb0fd, #3498db);
  background-image: -o-linear-gradient(top, #3cb0fd, #3498db);
  background-image: linear-gradient(to bottom, #3cb0fd, #3498db);
  text-decoration: none;
}














/* ------MEDIA QUERY */

@media screen and (max-width: 900px) {
    .cover-text{
        font-size: 0.7rem;
        margin: 0rem 3rem;
    }
  }