body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.navbar{
    height: 5rem;
    padding-top: 1rem;
    background: rgb(255,70,70);
    background: linear-gradient(90deg, rgba(255,70,70,1) 0%, rgba(255,153,0,1) 38%, rgba(105,210,83,1) 90%);
    font-family: 'Merriweather', serif;
    font-size: 1.5rem;
}

.navbar-text{
    text-align: center;
    font-weight: 700;
    border: 0.1px solid white;
    padding: 0rem 1rem;
}
.cover{
    /* height: 25rem; */
    text-align: center;
    padding: 8rem 0rem 6rem 0rem;
    background: repeat-x;
    background-image: url('https://img.freepik.com/free-photo/christmas-background-with-snowflakes-glowing-stars-designs_1048-13430.jpg?w=1380&t=st=1686480704~exp=1686481304~hmac=3c45517e95d48a1559fd88fa14d10675e4567bc1af809fe7845b2dd2fdc21a05');
    border-top-left-radius: 2rem;
    border-top-right-radius: 2rem;
    font-family: 'Merriweather', serif;
}

.cover-heading1{
    font-size: 3rem;
    color: gray;
    font-weight: bolder;
}

.cover-heading2{
    font-size: 3.5rem;
    color: green;
    font-weight: bolder;
}

.cover-text{
    font-size: 0.8rem;
    margin: 0rem 20rem;
}

.cover-button{
    padding-top: 1rem;
}

.cover-btn{
    background: linear-gradient(86.31deg, #FF5C6B 0%, #EB9898 99.29%);
    padding: 0.8rem 2.4rem;
    border-radius: 0.5rem;
    font-size: 1.2rem;
    color: white;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    line-height: 136%;
    text-align: center;
    letter-spacing: -0.02em;
}

.cover-btn:hover{
    background: #3cb0fd;
  background-image: linear-gradient(to bottom, #3cb0fd, #3498db);
  text-decoration: none;
}














/* ------MEDIA QUERY */

@media screen and (max-width: 900px) {
    .cover-text{
        font-size: 0.7rem;
        margin: 0rem 3rem;
    }
  }
.services{
    background-color: #FAF5E4;
    text-align: center;
    padding-bottom: 3rem;
}

.head1{
    padding: 1rem 0rem 0rem 0rem;
}

h1{
    margin: 0rem 0rem 0rem 0rem;
}

hr{
    margin-top: 1rem;
    width: 10rem;
}

.cards{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    padding: 0rem 0rem;
}

.card{
    height: 17rem;
    background-color: aliceblue;
    margin: 2rem;
    border-radius: 1.5rem;
}

.card-head{
    font-weight: 600;
    font-size: larger;
    color: #181D4E;
}

.card-text{
    color: #646672;
    padding: 1rem;
}

.card-icon{
    height: 7rem;
    padding: 1rem;
}


.services-btn{
    background: linear-gradient(86.31deg, #FF5C6B 0%, #EB9898 99.29%);
    padding: 0.8rem 2.4rem;
    border-radius: 0.5rem;
    font-size: 1.2rem;
    color: white;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    line-height: 136%;
    text-align: center;
    letter-spacing: -0.02em;
}

.services-btn:hover{
    background: #3cb0fd;
  background-image: linear-gradient(to bottom, #3cb0fd, #3498db);
  text-decoration: none;
}

/* ------MEDIA QUERY */

@media screen and (max-width: 900px) {
    .cards{
        grid-template-columns: auto;
    }

    .card{
        width: 20rem;
    }

  }
.wedo{
    text-align: center;
    padding: 5rem 0rem;
}

.wedo-head{
    padding: 10rem 8rem 10rem 8rem;
    font-size: 4rem;
    text-shadow: 0.1rem 0.1rem gray;
    font-family: 'Geologica', sans-serif;
}



.wedo-btn{
    background: linear-gradient(86.31deg, #FF5C6B 0%, #EB9898 99.29%);
    padding: 0.8rem 2.4rem;
    border-radius: 0.5rem;
    font-size: 1.2rem;
    color: white;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    line-height: 136%;
    text-align: center;
    letter-spacing: -0.02em;
}

.wedo-btn:hover{
    background: #3cb0fd;
  background-image: linear-gradient(to bottom, #3cb0fd, #3498db);
  text-decoration: none;
}

/* ------MEDIA QUERY */

@media screen and (max-width: 900px) {
    .wedo-head{
        font-size: 2rem;
    }

    .wedo-btn{
        width: 9rem;
        padding: 0.6rem;
    }
  }
.project{
    text-align: center;
    padding: 5rem;
    background-color: #FAF5E4;
}

.project-head{
    font-family: 'Ubuntu', sans-serif;
    margin-bottom: 5rem;
}


.slide{
    width: 50rem;
    margin-left: 18rem;
}





/* ------MEDIA QUERY */

@media screen and (max-width: 1300px) {
    .slide{
        width: auto;
        margin-left: 0rem;
    }
  }
.pricing{
    padding: 5rem 1rem;
}

.cards{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

.pricing-head{
    font-size: 3rem;
    text-align: center;
    font-weight: 700;
    font-family: 'Ubuntu', sans-serif;
}

.card{
    width: 25rem;
    border: 0.3rem solid black;
}

.card-head{
    padding: 1rem 0rem;
    font-size: 2rem;
    font-weight: 700;
    text-align: center;
    font-family: 'Ubuntu', sans-serif;
}

.card-price{
    font-size: 2rem;
    font-weight: 700;
    text-align: center;
}


.price-ul{
    list-style: none;
}

.active{
    font-size: 1.2rem;
    font-weight: 500;
    list-style-type: disc;
}

.inactive{
    font-size: 1.2rem;
    font-weight: 500;
    color: gray;
}

.c1{
    background-color: rgb(200, 255, 255);
}

.c2{
    background-color: rgb(227, 173, 85);
}

.c3{
    background-color: whitesmoke;
}

.button{
    text-align: center;
    padding: 2rem 0.5rem;
}

.price-btn{
    background: linear-gradient(86.31deg, #FF5C6B 0%, #EB9898 99.29%);
    width: 15rem;
    padding: 0.8rem 2.4rem;
    border-radius: 0.5rem;
    font-size: 1.2rem;
    color: white;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    line-height: 136%;
    text-align: center;
    letter-spacing: -0.02em;
}

.price-btn:hover{
    background: #3cb0fd;
  background-image: linear-gradient(to bottom, #3cb0fd, #3498db);
  text-decoration: none;
}

@media screen and (max-width: 900px) {

    .pricing{
        padding: 2rem 0rem;
    }
    .cards{
        grid-template-columns: auto;
    }

    .card{
        width: 20rem;
    }

    .price-btn{
        width: 13rem;
    }
  }

  @media screen and (min-width: 700px) {
    .cards{
        grid-template-columns: auto;
    }

    .card{
        width: 40rem;
    }

    .price-btn{
        width: 13rem;
    }
  }

  @media screen and (min-width: 1200px) {

    .cards{
        grid-template-columns: auto auto auto;
    }

    .card{
        width: 20rem;
    }

    .price-btn{
        width: 13rem;
    }
  }
.main-foot{
    padding: 2rem;
    background-color:#FAF5E4;
    text-align: center;
}

.footer{
    display: flex;
}

.address-head{
    font-size: 1.5rem;
    font-weight: 600;
    display:inline;
    border: 0.1rem solid black;
    padding: 0.3rem;
    border-radius: 0.2rem;
}

.add{
    color: #646672;

    padding: 1rem;
}

.add-head{
    font-size: large;
    font-weight: 800;
}

.icons{
    list-style: none;
}

.icons li{
    display: inline;
    padding: 0.3rem;
}

.address{
    flex-grow: 4;
}

.options{
    flex-grow: 2;
    color: #646672;

}

.option_name{
    list-style: none;
}

.option_name li{
    display: inline;
    padding: 2.5rem;
    margin: 2rem;
}

.foot{
    text-align: center;
    font-size: 0.7rem;
    color: #646672;
}






/* Responsive layout - makes a one column layout instead of a two-column layout */
@media (max-width: 900px) {
    .footer {
      flex-direction: column;
    }

    .option_name li{
        display: block;
        padding: 0rem;
        margin: 0rem;
    }

    .main-foot{
        padding: 1rem;
        background-color:#FAF5E4;
    }
  }
